ul {
    display: grid;
    grid-template-columns: repeat(5, auto) 0;
}
ul li {
    width:auto;
    display: inline-block;
}

.item {
    width: 40px;
    height: 18px;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 500;
    line-height: 1.29;
    color: #9d9aa8;
}

.item a{
    color: black;
    text-decoration: none;

}

.activeItem {
    width: 48px;
    height: 18px;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 600;
    line-height: 1.29;
    text-align: center;
    color: #ffffff;
}


