*, *:after, *:before {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    transition: .1s ease-in-out; /* добавим плавность переходов для всех элементов страницы*/
}

body {
    height: 100%;
    background: #f6f6fa;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active  {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
}

.mainPage {
    display: grid;
    grid-template-areas: ". icon navbar userLog ."
                         ". wraper wraper wraper .";
    grid-template-rows: 88px auto;
    grid-template-columns: 11% 20% 37% 20% 11%;
    grid-gap: 1%;
    /*
    grid-column-gap: 0.3em;
    grid-row-gap: 0.2em;
    */
}

.head {
    width: 1920px;
    height: 88px;
    box-shadow: 0 10px 10px 0 rgba(0, 0, 0, 0.05);
    background-color: #1a122d;
    z-index: -1;
}

.icon {
    grid-area: icon;
    align-self: center;
}

.nav {
    grid-area: navbar;
    align-self: center;
    margin-left: 60px;
}

.userLog {
    grid-area: userLog;
    align-self: center;
}

.appWrapperContent {
    grid-area: wraper;
    background-color: white;
}

.preloader {
    margin-left: 20px
}


