.postsBlock{
    padding: 15px;
}

.posts {
    margin-top: 10px;
}


.form {
    width: 700px;
    height: 40px;

    display: grid;
    grid-template-areas: "area but";
}

.area {
    grid-area: area;
    resize: none;
    width: 600px;
    height: 40px;
    border-radius: 25px 0 0 25px;
    border: none;
    background-color: #f6f6fa;
    font-size: 14px;
    padding: 10px;
    outline: none;
    display: inline-block;
}

.button {
    grid-area: but;
    width: 100px;
    height: 40px;
    background-color: #6d57f6;
    border-radius: 0 25px 25px 0;
    border: none;
    cursor: pointer;
    outline: none;
    font-size: 14px;
    color: white;
    display: inline-block;
}

@media screen and (max-width: 600px){
    .button {
        width: 60px;
        height: 20px;
        font-size: 14px;
    }
}